import { extend, configure } from 'vee-validate';
import { required } from 'vee-validate/dist/rules';

configure({
	classes: {
		invalid: 'is-invalid',
	}
});

extend('spaces', {
	validate(value) {
		return value.length >= 3 && !/[A-ZА-Яа-я\s]/.test(value);
	},
	message: 'Только латинские строчные буквы, цифры и знак подчеркивания.'
});

extend('url', {
	validate(value) {
		// eslint-disable-next-line no-useless-escape
		return /^(http(s):\/\/.)[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)$/g.test(value);
	},
	message: 'Неправильный URL паттерн'
});

extend('empty-array', {
	validate(value) {
		return !!value.length;
	},
	message: 'Необходимо выбрать хотя бы 1 элемент',
	computesRequired: true
});

extend('required', {
	...required,
	message: 'Обязательно',
	computesRequired: true
});

extend('min', {
	validate(value, args) {
		if(value.length >= args.length) return true;
		return `Должно быть не менее ${args.length} символов`;
	},
	params: ['length']
});

extend('max', {
	validate(value, args) {
		if(value.length < args.length) return true;
		return `Должно быть не менее ${args.length} символов`;
	},
	params: ['length']
});