const state = {
	templatesList: [],
	clientsList: [],
	userRole: '',
	userId: '',
	activeDashboard: '',
	activeDashboardCategory: 'list',
	activeNestedDashboard: '',
	activeNestedDashboardCategory: 'list',
};
const getters = {
	getTemplates: (state) => state.templatesList,
	getClients: (state) => state.clientsList,
	getActiveDashboard: (state) => state.activeDashboard,
	getActiveDashboardCategory: (state) => state.activeDashboardCategory,
	getActiveNestedDashboard: (state) => state.activeNestedDashboard,
	getActiveNestedDashboardCategory: (state) => state.activeNestedDashboardCategory,
	getUserId: (state) => state.userId,
	getMyRole: (state) => state.userRole
};
const mutations = {
	setTemplate(state, newTemplates) {
		if (Array.isArray(newTemplates)) {
			state.templatesList = newTemplates;
		} else if (typeof newTemplates === 'object') {
			state.templatesList.unshift(newTemplates);
		}
	},
	deleteTemplate(state, deletableTemplateId) {
		state.templatesList = state.templatesList.filter(template => template.template_id !== deletableTemplateId);
	},
	setClient(state, newClients) {
		if (Array.isArray(newClients)) {
			state.clientsList = newClients;
		} else if (typeof newClients === 'object') {
			state.clientsList.unshift(newClients);
		}
	},
	setActiveDashboard(state, newDashboard) {
		state.activeDashboard = newDashboard;
	},
	setActiveDashboardCategory(state, newCategory) {
		state.activeDashboardCategory = newCategory;
	},
	setActiveNestedDashboard(state, newDashboard) {
		state.activeNestedDashboard = newDashboard;
	},
	setActiveNestedDashboardCategory(state, newCategory) {
		state.activeNestedDashboardCategory = newCategory;
	},
	setMyRole(state, role) {
		state.userRole = role;
	},
	setUserId(state, userId) {
		state.userId = userId;
	},
	resetStore(state) {
		state.templatesList = [];
		state.clientsList = [];
		state.userRole = '';
		state.userId = '';
		state.activeDashboard = '';
		state.activeDashboardCategory = 'list';
		state.activeNestedDashboard = '';
		state.activeNestedDashboardCategory = 'list';
	}
};
const actions = {};

export default {
	state,
	getters,
	mutations,
	actions,
};