/* eslint-disable no-unused-vars */
import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store/index';

import role from '@/middleware/role';

const { push } = VueRouter.prototype;

const { isNavigationFailure, NavigationFailureType } = VueRouter;

VueRouter.prototype.push = function (location) {
	return push.call(this, location).catch(error => {
		if (!isNavigationFailure(error, NavigationFailureType.duplicated)) {
			throw Error(error);
		}
	});
};

Vue.use(VueRouter);

const routes = [
	{
		path: '*',
		component: () => import(/* webpackChunkName: "404" */ '@/views/NotFoundView.vue'),
	},
	{
		path: '/',
		name: 'Home',
		meta: {
			middleware: role
		},
		beforeEnter(to, from, next) {
			const myRole = store.getters.getMyRole;
			if(to.name === 'Home') {
				if(myRole === 'admin') {
					if (to.name !== 'clients') return next('/clients');
					else return next();
				} else if (myRole === 'user') {
					if (to.name !== 'templates') return next('/templates');
					else return next();
				}
			}
			next();
		},
		component: () => import(/* webpackChunkName: "home" */ '../views/HomeView.vue'),
		children: [
			{
				path: '/clients',
				name: 'clients',
				props: true,
				component: () => import(/* webpackChunkName: "clientsView" */ '../views/DashboardView.vue'),
				meta: {
					middleware: role
				},
				beforeEnter(to, from, next) {
					const myRole = store.getters.getMyRole;
					if(to.name === 'clients') {
						if(myRole === 'admin') {
							store.commit('setActiveDashboard', 'clients');
							return next();
						} else if (myRole === 'user') {
							return next('/templates');
						}
					}
					next();
				},
				children: [
					{
						path: ':userId/templates',
						name: 'clientsTemplates',
						props: true,
						component: () => import(/* webpackChunkName: "usersTemplates" */ '../views/TemplateView.vue'),
						meta: {
							middleware: role
						},
						beforeEnter(to, from, next) {
							const myRole = store.getters.getMyRole;
							if(myRole === 'admin') {
								return next();
							} else if (myRole === 'user') {
								return next('/');
							}
						},
					},
					{
						path: ':userId/reports',
						name: 'clientsReports',
						props: true,
						component: () => import(/* webpackChunkName: "usersReports" */ '../views/ReportView.vue'),
						meta: {
							middleware: role
						},
						beforeEnter(to, from, next) {
							const myRole = store.getters.getMyRole;
							if(myRole === 'admin') {
								return next();
							} else if (myRole === 'user') {
								return next('/');
							}
						},
					}
				]
			},
			{
				path: '/templates',
				name: 'templates',
				component: () => import(/* webpackChunkName: "templatesList" */ '../components/TemplateList.vue'),
				meta: { 
					middleware: role
				},
			},
			{
				path: '/reports',
				name: 'reports',
				component: () =>
					import(/* webpackChunkName: "templatesList" */ '../views/DashboardView.vue'),
				meta: {
					middleware: role
				},
			},
			{
				path: '/new-template',
				name: 'newTemplate',
				component: () =>
					import(/* webpackChunkName: "newTemplate" */ '../views/newTemplateView.vue'),
			},
		]
	},
	{
		path: '/login',
		name: 'Login',
		component: () =>
			import(/* webpackChunkName: "login" */ '../views/LoginView.vue'),
	},
];


const router = new VueRouter({
	// mode: 'history',
	routes,
});

router.beforeEach((to, from, next) => {
	if (!to.meta.middleware) {
		return next();
	}
	const middleware = to.meta.middleware;
	const context = {
		to,
		from,
		next,
		store
	};
	return middleware({
		...context
	});
});

router.beforeEach((to, from, next) => {
	if(to.name === 'clients') store.commit('setActiveDashboard', 'clients');
	if(to.name === 'reports') store.commit('setActiveDashboard', 'reports');
	if(to.name === 'templates') store.commit('setActiveDashboard', 'templates');
	if(to.name === 'clientsTemplates') {
		store.commit('setActiveDashboard', 'clients');
		store.commit('setActiveNestedDashboard', 'clientsTemplates');
	}
	if(to.name === 'clientsReports') {
		store.commit('setActiveDashboard', 'clients');
		store.commit('setActiveNestedDashboard', 'clientsReports');
	}
	next();
});

export default router;
