import { HTTP } from '@/helpers/httpCommon';
import store from '@/store/index';

export default function role({ next }) {
	const role = store.getters.getMyRole;

	if(!role) {
		HTTP.get('user/get/info')
			.then((response) => {
				const myRole = response.data.user_role;
				store.commit('setMyRole', myRole);
				return next();
			});
	} else {
		return next();
	}
}