import Vue from 'vue';

const state = {
	subjectIds: [],
	template: {
		id: 0,
		name: '',
		type: 'OPERATOR',
		channelType: 'WHATSAPP',
		content: {
			text: '',
		},
		language: '',
		category: '',
	},
	templateExamples: {
		headerExampleMediaUrl: '',
		urlPostfix: '',
		urlTextExample: '',
		textWithParams: '',
		textExampleParams: []
	},
	contentSchema: {
		headers: {
			text: {
				text: '',
				headerType: 'TEXT',
				headerExampleTextParam: ''
			},
			video: {
				headerType: 'VIDEO',
				headerExampleMediaUrl: ''
			},
			image: {
				headerType: 'IMAGE',
				headerExampleMediaUrl: ''
			},
			document: {
				headerType: 'DOCUMENT',
				headerExampleMediaUrl: ''
			}
		},
		keyboard: {
			rows: [
				{
					buttons: []
				}
			]
		},
		button: {
			QUICK_REPLY: {
				text: '',
				buttonType: 'QUICK_REPLY',
				payload: ''
			},
			URL: {
				text: '',
				url: '',
				buttonType: 'URL',
				urlTextExample: '',
				urlPostfix: ''
			},
			PHONE: {
				text: '',
				buttonType: 'PHONE',
				phone: ''
			}
		},
		footer: {
			text: ''
		},
	},
};
const getters = {
	getSubjectIds: (state) => state.subjectIds,
	getTemplate: (state) => state.template,
	getContentSchema: (state) => state.contentSchema,
	getTemplateExamples: (state) => state.templateExamples
};
const mutations = {
	setSubjectIds(state, arrayIds) {
		state.subjectIds = arrayIds;
	}, 
	setTemplateId(state, id) {
		state.template.id = id;
	},
	setTemplateName(state, name) {
		state.template.name = name;
	},
	setTemplateType(state, type) {
		state.template.type = type;
	},
	setTemplateText(state, text) {
		state.template.content.text = text;
	},
	setTemplateCategory(state, category) {
		state.template.category = category;
	},
	setTemplateLanguage(state, language) {
		state.template.language = language;
	},
	setTemplateHeader(state, header) {
		Vue.set(state.template.content, 'header', header);
	},
	removeTemplateHeader(state) {
		Vue.delete(state.template.content, 'header');
	},
	setTemplateHeaderText(state, text) {
		state.template.content.header.text = text;
	},
	setTemplateFooter(state, footer) {
		Vue.set(state.template.content, 'footer', footer);
	},
	removeTemplateFooter(state) {
		Vue.delete(state.template.content, 'footer');
	},
	setTemplateFooterText(state, text) {
		state.template.content.footer.text = text;
	},
	setTemplateKeyboard(state, keyboard) {
		Vue.set(state.template.content, 'keyboard', keyboard);
	},
	removeTemplateKeyboard(state) {
		Vue.delete(state.template.content, 'keyboard');
	},
	setTemplateButtons(state, buttons) {
		state.template.content.keyboard.rows[0].buttons = buttons;
	},
	removeTemplateButton(state, index) {
		state.template.content.keyboard.rows[0].buttons.splice(index, 1);
	},
	setTextWithParams(state, text) {
		state.templateExamples.textWithParams = text;
	},
};
const actions = {
};

export default {
	state,
	getters,
	mutations,
	actions,
};